.redux-product {
    margin-bottom: 15px;
    border: 1px solid #ccc;
    background: #fff;
    padding: 0px 20px;
    min-width: 350px;
    float: left;
    margin-right: 20px;
    .name {
        color: #23282d;
        font-size: 32px;
        font-weight: 100;
        margin: 10px 0 0;
        line-height: 1.3;
        word-wrap: break-word;
        overflow-wrap: break-word;
        text-align: left;
    }
    .version {
        color: #72777c;
        font-size: 13px;
        font-weight: 400;
        float: none;
        display: inline-block;
        margin-left: 10px;
    }
    .author {
        margin: 15px 0 25px;
        color: #72777c;
        font-size: 16px;
        font-weight: 400;
        line-height: inherit;
    }
    .type {
        margin-left: 5px;
        background-color: #f0ad4e;
        display: inline;
        padding: 0.2em 0.5em 0.2em;
        font-weight: 400;
        line-height: 1;
        font-size: 12px;
        color: #fff !important;
        text-align: center;
        white-space: nowrap;
        vertical-align: baseline;
        border-radius: 0.25em;
        &.theme {
            background-color: #0099d5;
        }
    }
}
.hide {
    display: none !important;
}
